@import "../variables";
.ladiesPage {
  width: 100%;
  overflow: hidden;
  background-image: url("../../../assets/images/ladybg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  .topButton {
    display: none;
  }
  .content {
    display: flex;
    min-height: calc(100vh - 178px);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 140px 20px;
    @media only screen and (max-width: 600px) {
      padding: 30px 20px;
    }
    .mainTile {
      font-family: $MontserratBold;
      font-style: normal;
      font-weight: bold;
      font-size: 35px;
      line-height: 45px;
      color: #ffffff;
      text-align: center;
      @media only screen and (max-width: 600px) {
        font-size: 22px;
        line-height: 27px;
      }
    }
    .subTitle {
      font-family: $Montserrat;
      font-style: normal;
      font-weight: 600;
      font-size: 30px;
      line-height: 37px;
      color: #ffffff;
      margin-top: 20px;
      @media only screen and (max-width: 600px) {
        font-size: 18px;
        line-height: 22px;
        margin-top: 20px;
        text-align: center;
        max-width: 200px;
      }
    }
    .icons {
      cursor: pointer;
      display: flex;
      align-items: center;
      margin-top: 50px;
      .img1 {
        width: 70px;
        height: 70px;
        object-fit: contain;
        @media only screen and (max-width: 600px) {
          width: 45px;
          height: 45px;
        }
      }
      .countInp {
        width: 326px;
        height: 70px;
        background-image: url("../../../assets/images/nnn.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background-color: transparent;
        border: none;
        outline: none;
        text-align: center;
        font-family: $MontserratBold;
        font-style: normal;
        font-weight: bold;
        font-size: 30px;
        line-height: 43px;
        color: #ffffff;
        &::placeholder {
          font-family: $MontserratBold;
          font-style: normal;
          font-weight: bold;
          font-size: 35px;
          line-height: 43px;
          color: #ffffff;
        }
        @media only screen and (max-width: 600px) {
          width: 188px;
          height: 45px;
          margin: 0 5px;
          font-size: 18px;
        }
      }
      .img2 {
        width: 326px;
        height: 70px;
        object-fit: contain;
        margin: 0 50px;
        @media only screen and (max-width: 600px) {
          width: 188px;
          height: 45px;
          margin: 0 5px;
        }
      }
      .img3 {
        width: 70px;
        height: 70px;
        object-fit: contain;
        @media only screen and (max-width: 600px) {
          width: 45px;
          height: 45px;
        }
      }
    }
    .pageButton {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 35px;
      height: 45px;
      font-family: $Montserrat;
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      color: #ffffff;
      margin-top: 50px;
      border: none;
      outline: none;
      background: linear-gradient(90deg, #a10b9e 0%, #d54f04 100%);
      border-radius: 60px;
    }
    .text {
      font-family: $Montserrat;
      font-style: normal;
      font-weight: normal;
      font-size: 17px;
      line-height: 21px;
      text-align: center;
      color: #ffffff;
      width: 100%;
      max-width: 941px;
      margin-top: 50px;
    }
    .note {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 50px;
      @media only screen and (max-width: 600px) {
        flex-direction: column;
        align-items: center;
      }
      .title {
        max-width: 363px;
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        color: #ffffff;
        margin-right: 20px;
        @media only screen and (max-width: 600px) {
          max-width: 100%;
          width: 100%;
          margin-right: 0;
          font-size: 20px;
          line-height: 24px;
          text-align: center;
        }
      }
      img {
        width: 232px;
        height: 48px;
        object-fit: contain;
        @media only screen and (max-width: 600px) {
          margin-top: 30px;
        }
      }
      .tokenId {
        width: 232px;
        height: 48px;
        object-fit: contain;
        background-image: url("../../../assets/images/nnn.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background-color: transparent;
        border: none;
        outline: none;
        text-align: center;
        font-family: $MontserratBold;
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 43px;
        color: #ffffff;
        &::placeholder {
          font-family: $MontserratBold;
          font-style: normal;
          font-weight: bold;
          font-size: 20px;
          line-height: 43px;
          color: #ffffff;
        }
        @media only screen and (max-width: 600px) {
          margin-top: 30px;
        }
      }
    }
  }
}
